@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{height:100%;font-weight:800}body{background:#030321;font-family:Arial}svg{display:block;font:10.5em 'Montserrat';width:960px;height:300px;margin:0 auto}.text-copy{fill:none;stroke:white;stroke-dasharray:6% 29%;stroke-width:5px;stroke-dashoffset:0%;-webkit-animation:stroke-offset 5.5s infinite linear;animation:stroke-offset 5.5s infinite linear}.text-copy:nth-child(1){stroke:#4D163D;-webkit-animation-delay:-1;animation-delay:-1}.text-copy:nth-child(2){stroke:#840037;-webkit-animation-delay:-2s;animation-delay:-2s}.text-copy:nth-child(3){stroke:#BD0034;-webkit-animation-delay:-3s;animation-delay:-3s}.text-copy:nth-child(4){stroke:#BD0034;-webkit-animation-delay:-4s;animation-delay:-4s}.text-copy:nth-child(5){stroke:#FDB731;-webkit-animation-delay:-5s;animation-delay:-5s}@-webkit-keyframes stroke-offset{100%{stroke-dashoffset:-35%}}@keyframes stroke-offset{100%{stroke-dashoffset:-35%}}

